@import url(https://fonts.googleapis.com/css2?family=Raleway&family=Satisfy&display=swap);
/* @import url('https://fonts.googleapis.com/css2?family=Raleway&display=swap'); */

body {
  margin: 0px;
  font-family: 'Raleway', sans-serif;
}

.e-title {
  margin-left: 1rem;
}

.loading-text {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh
}

* {
  /* border: solid 1px red; */
}
.app__nav{background-color:#ab457a;margin:0px;display:flex;align-items:flex-end;height:150px}.app__nav-logo{font-size:4rem;padding:1rem;margin:.5rem;margin-left:1rem;font-family:"Satisfy",cursive;background-color:#fff;border-radius:15px;color:#ab457a}.app__nav-links{margin-right:auto;margin-left:4rem;margin-bottom:1.3rem;display:flex}.app__nav-links .links a{text-decoration:none;font-size:1.2rem;padding:5px;margin:0 1rem;cursor:pointer;color:#fff}.app__nav-links .links a:hover{color:#000}.app__nav-profile .profile-true{margin:1rem 2rem;display:flex;flex-direction:column;justify-content:center;align-items:center}.app__nav-profile .profile-true .profile-address{cursor:pointer;background-color:#000;color:#fff;padding:3px 5px;border-radius:5px;text-decoration:none;font-weight:600}.app__nav-profile .profile-true .profile-icon{cursor:pointer;margin-bottom:5px}.app__nav-profile .profile-false{cursor:pointer;background-color:green;color:#fff;padding:.5rem 1rem;margin:1rem;border-radius:10px}
.app__main{min-height:90vh;padding:2rem}.app__main-title{font-size:1.8rem;margin-left:1rem}.app__main-items{padding-top:1.5rem;display:flex;flex-wrap:wrap}
.app__footer{background-color:#ab457a;width:100%;height:50px;display:flex;justify-content:center;align-items:center}
.card{height:400px;width:330px;border:solid 1px #bf00ff;border-radius:5px;display:flex;flex-direction:column;justify-content:flex-end;align-items:center;position:relative;margin:1rem}.card-image{position:relative;height:100%;width:100%;border-radius:10px}.card-image img{width:100%;height:100%}.card-content{opacity:0;position:absolute;height:70%;width:auto;display:flex;flex-direction:column;justify-content:space-around;align-items:center;padding:0 1rem;background-color:#fff;transition:all 1s ease;border:solid 1px gray;border-top-left-radius:15px;border-top-right-radius:15px}.card-content .content-text{display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:center;width:100%}.card-content .content-text .content-title{font-size:1.3rem;margin:.5rem}.card-content .content-action{display:flex;justify-content:space-between;width:290px}.card-content .content-action .price{display:flex;justify-content:center;align-items:center;font-size:1.3rem}.card-content .content-action .price img{width:35px;height:35px}.card-content .content-action .buy-btn{background-color:#545454;color:#fff;padding:.8rem 2rem;border-radius:5px;font-weight:600;cursor:pointer}.card-content .content-action .buy-btn:hover{background-color:#000}.card-content:hover{opacity:1}.card-attrib{width:100%;display:flex;justify-content:space-around;align-items:center}.card-attrib .attrib-div .attrib-name{text-align:center;font-family:monospace;background-color:#c1c1c1;border-radius:5px;padding:2px}.card-attrib .attrib-div .attrib-value{text-align:center}

.app__lab{display:flex;justify-content:center;align-items:center;min-height:85vh;display:flex;flex-direction:column;padding:1.3rem}.app__lab-title{font-size:1.8rem;margin-left:1rem}.lab-form{width:600px;min-height:810px;border:solid 1px gray;margin:1rem;border-radius:10px;background-color:rgba(88,0,126,.718)}.lab-form .form-subtitle{font-size:1.3rem;padding-left:.7rem}.lab-form .form-div{width:100%;background-color:rgba(174,174,174,.361);margin:1rem 0;border-radius:5px;height:100px}.lab-form .form-div .form-title{font-size:1.2rem;padding:10px 0 5px 10px}.lab-form .form-div input{height:30px;width:589px;outline:none;border-color:rgba(128,128,128,.36);font-size:1.1rem;padding-left:7px;font-family:inherit}.lab-form-bottom{display:flex;justify-content:flex-end}.lab-form-bottom .close-btn{font-size:1.3rem;margin:10px;background-color:#000;color:#fff;padding:7px 10px;border-radius:5px;cursor:pointer}.lab-form-bottom .close-btn:active{background-color:#5e5e5e}.lab-form-bottom .mint-btn{font-size:1.3rem;margin:10px;background-color:#000;color:#fff;padding:7px 10px;border-radius:5px;cursor:pointer}.lab-form-bottom .mint-btn:active{background-color:#5e5e5e}
.app__profile{min-height:90vh;padding:2rem}.app__profile-title{font-size:1.8rem;margin-left:1rem}.app__profile-items{padding-top:1.5rem;display:flex;flex-wrap:wrap}
